import axios from 'axios';

export const http = axios.create({
  headers: {
    'Content-Type': 'application/json',
  },
});

export const apiUrl = (method, params = []) => {
  const origin = 'https://mbystrzycki.pl';

  return `${origin}/${method.toLowerCase()}/${params.join('/')}`;
};



